.tabs{
    display: flex;
    height: 9vh;
    align-items: center;
    width: 100%;
    position: absolute;       
    z-index: 1; 
    box-shadow: 0px 5px 5px gray;
}

.tabContent, .noTabsContent{
    color: var(--darker-grey);
    font-family: "Lexend Deca";
    overflow-y: scroll;
    position: absolute;
    height: 65vh;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    background-color: var(--white);
    padding-bottom: 20px;
}

.tabContent{
    top: 33vh;
}

.tabs, .noTabsContent{
    top: 24vh;
}

.tabType{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: var(--white);
    border-bottom: 1px solid var(--light-grey);
    border-left: 1px solid var(--medium-grey);
}

.tabSelected{
    background-color: var( --main-color);
    color: var(--white) !important;
    border-bottom: 1px solid var( --main-color);
}

.tabSelected svg{
    color: var(--white) !important;
}


.InfoTab, .ARTab, .VRTab{
    display: flex;
    justify-content: center;
    width: 90%;
    padding-top: 15px;
    padding-bottom: 15px;
    flex-direction: column;

}

.tabBtn{
    display: flex;
    flex-direction: column;
    padding: 10%;
}

.tabIcon{
    border: none;
    color: var(--darker-grey);
    font-size: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.tabTitle{
    width: 100%;
    height: 20%;
    font-size: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
}

.ARSondagem4, .VRSondagem4, .InfoSondagem{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 15px;
}

.raTitle, .vrTitle{
    font-size: 18px;
    font-weight: bold;
}